import React, { Component } from 'react';

import Nav from '../component/NavArea';
import {TitleElement} from '../component/focusTitle';
import ImageList from '../component/ImageList';
import BenchmarkPage from './BenchmarkPage';
import JetStreamPage from './JetStreamPage';
import NoCache1 from '../component/NoCache1.html';
import NoCache2 from '../component/NoCache2.html';
import NoCache3 from '../component/NoCache3.html';
import NoCache4 from '../component/NoCache4.html';


class HomePage extends Component {
    constructor(props, context) {
        super(props, context);

        this.landscapePlayer = this.landscapePlayer.bind(this);
        this.portraitPlayer = this.portraitPlayer.bind(this);
        this.selectedNav = this.selectedNav.bind(this);
        

        this.state = {
            contentTitle: 'Video Title 1',
            contentDetail:'A military contractor reconnects with an old flame in Hawaii, then falls for a hard-nosed pilot.',
            currentNav: 'scenery'
        }
        
    }
    selectedNav() {
        
        if(typeof this.props.currentFocus === 'string') {
            let key = this.props.currentFocus.split('-')[1]
            this.setState({
                currentNav: key
            })
        }        
    }

    /*shouldComponentUpdate(nextProps, nextState) {
        console.log(this.state.bgUrl)
        console.log(nextState)
        return this.state.value !== nextState.value;
    }*/

    landscapePlayer() {
        this.props.startPlayer('LANDSCAPE');
    }
    portraitPlayer() {
        this.props.startPlayer('PORTRAIT');
    }

    render() {
        const {contentTitle, contentDetail, currentNav} = this.state;

        let isLandscape = this.props.orientation === 'LANDSCAPE';

        const InfoArea = (props) => {
            const {name, title, txt} = props;
            return (
                <div className={name}>
                    <div className="info-title">{title}</div>
                    <div className="info-txt">{txt}</div>
                </div>
            )
        }

        const LandscapeListView = () => {
            return (
                <div>
                    <ImageList path={currentNav} mode='LANDSCAPE' selected={this.landscapePlayer} currentFocus={this.props.currentFocus}/>
                </div>);
        } //<InfoArea name="info-area" title={contentTitle} txt={contentDetail}/>

        /*const PortraitListView = () => {
            
            let isFor, isDance, isConcert;
            let isMove = false;

            if(typeof this.props.currentFocus === 'string') {

                if(this.props.currentFocus.indexOf('concert') >=0) {
                    isMove = true;
                    isConcert = true;
                    isFor = false;
                    isDance = false;
                } else if(this.props.currentFocus.indexOf('foryou') >=0) {
                    isConcert = false;
                    isFor = true;
                    isDance = false;
                } else if(this.props.currentFocus.indexOf('dance') >=0) {
                    isConcert = false;
                    isFor = false;
                    isDance = true;
                }
            }

            let move = {
                transform: `translate3d(0,${isMove ?-600 : 0}px,0)`,
                transition: `transform 3s linear`,
            }

            return(
                <div className="portrait-list-area" style={move}>
                    <div className="portrait-list-container">
                        <TitleElement focusPath={`title-1`} title="For You" index="0" focused={isFor}/>
                        <ImageList path='foryou' mode='PORTRAIT' selected={this.portraitPlayer} currentFocus={this.props.currentFocus}/>
                        {true ? <InfoArea name="portrait-info" title={contentTitle} txt={contentDetail}/>: ''}
                    </div>
                    <div className="portrait-list-container">
                    <TitleElement focusPath={`title-2`} title="Dance" index="1" focused={isDance}/>
                        <ImageList path='dance' mode='PORTRAIT' currentFocus={this.props.currentFocus}/>
                        {true ? <InfoArea name="portrait-info" title={contentTitle} txt={contentDetail}/> : ''}
                    </div>
                    <div className="portrait-list-container">
                    <TitleElement focusPath={`title-3`} title="Concert" index="2" focused={isConcert}/>
                        <ImageList path='concert' mode='PORTRAIT' currentFocus={this.props.currentFocus}/>    
                        {isConcert ? <InfoArea name="portrait-info" title={contentTitle} txt={contentDetail}/> : ''}
                    </div>
            </div>
            )
        }*/
        let CacheAry = [NoCache1,NoCache2,NoCache3,NoCache4];
        const NoCacheView = () => {
            const lists = []
            for(let i = 0; i < 4; i++) {
                let height = `50${i}px`
                if( i == 0 ){
                    lists.push(
                        <iframe
                            srcDoc= {CacheAry[`${i}`]}
                            style={{ paddingTop: '200px', paddingLeft: '80px', width: '630px', border: '0px', height: `${height}`}}
                            sandbox="allow-same-origin allow-scripts allow-popups allow-forms allow-modals"
                            scrolling="auto"
                        />
                    )
                }else{
                lists.push(
                    <iframe
                        srcDoc={CacheAry[`${i}`]}
                        style={{ paddingTop: '200px', paddingLeft: '26px', width: '310px', border: '0px', height: `${height}` }}
                        sandbox="allow-same-origin allow-scripts allow-popups allow-forms allow-modals"
                        scrolling="auto"
                />
                )}
            }
            return lists;
        }


        const pageSwitch = (currentNav) => {
            switch (currentNav) {
                case 'scenery':
                    return (
                        <div>
                            <NoCacheView />
                            <LandscapeListView />
                        </div>
                    )
                case 'benchmark':
                    return <BenchmarkPage />;
                case 'jetstream':
                    return <JetStreamPage />;
                default:
                    return (
                            <div>
                                <NoCacheView />
                                <LandscapeListView />
                            </div>
                            )
            }
        };
        
        return (
        <div>
            <Nav isLandscape={true} currentFocus={this.props.currentFocus} selected={this.selectedNav}/>

            {pageSwitch(currentNav)}
        </div>
        );
    }
}

export default HomePage;