import React, { Component } from 'react';
import JetStream from './JetStream.html'

class JetStreamPage extends Component {

    render() {
        return(
            <div>
                <iframe
                    title="jetstream"
                    srcDoc={JetStream}
                    style={{ width: '100%', border: '0px', height: '1100px',  position: 'absolute', top:'212px'}}
                    sandbox="allow-same-origin allow-scripts allow-popups allow-forms allow-modals"
                    scrolling="auto"
                />
            </div>
        )
    }
}

export default JetStreamPage;